import React, { Component } from "react";
import { Link } from "react-router-dom";
import db from "../../helpers/localDB";
import { Collapse, Navbar, NavbarToggler, Nav, NavItem,NavLink } from "reactstrap";

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false
    };
  }

  toggle = e => {
    this.setState({
      isOpen: !this.state.isOpen
    });
  };

  render() {
    return (
      <Navbar className="navbar navbar-expand-md navbar-dark bg-primary">
        {/* <div className="snowflakes" aria-hidden="true">
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
          <div className="snowflake"></div>
        </div> */}

        <Link className="navbar-brand" to="/agent/home">
            
        </Link>
        {/* <p style={{color:"#9CF49C","margin-right": "8px"}}> </p> */}

        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <Link className="nav-link" to="/agent/drivers">
                
              </Link>
              {/* <NavLink href="/components/">Components</NavLink> */}
            </NavItem>

            <NavItem>
              <Link className="nav-link" to="/agent/passengers">
                
              </Link>
              {/* <NavLink href="/components/">Components</NavLink> */}
            </NavItem>

            <NavItem>
              <Link className="nav-link" to="/agent/trips/index">
                 
              </Link>
              {/* <NavLink href="/components/">Components</NavLink> */}
            </NavItem>

            <NavItem>
              <Link className="nav-link" to="/agent/price/rules">
                 
              </Link>
              {/* <NavLink href="https://github.com/reactstrap/reactstrap">GitHub</NavLink> */}
            </NavItem>

            <NavItem>
              <Link className="nav-link" to="/agent/discounts/index">
                 
              </Link>
              {/* <NavLink href="https://github.com/reactstrap/reactstrap">GitHub</NavLink> */}
            </NavItem>

            <NavItem>
              <Link className="nav-link" to="/agent/trips/comments">
                 
              </Link>
              {/* <NavLink href="https://github.com/reactstrap/reactstrap">GitHub</NavLink> */}
            </NavItem>

            <NavItem>
              <Link className="nav-link" to="/agent/trips/reserved">
                   
              </Link>
              {/* <NavLink href="https://github.com/reactstrap/reactstrap">GitHub</NavLink> */}
            </NavItem>


            <NavItem>
              <Link className="nav-link" to="/agent/trips/request">
                 
              </Link>
              {/* <NavLink href="https://github.com/reactstrap/reactstrap">GitHub</NavLink> */}
            </NavItem>


            <NavItem>
              
               <NavLink href="http://zapptaxi.com:3001/app/android/driver.apk">  </NavLink> 
            </NavItem>


            <NavItem>
              <form className="form-inline">
                <button
                  className="nav-link btn btn-danger p-2"
                  href="/panel/login"
                  type="button"
                  onClick={() => {
                    db.set("token", "").write();
                    db.set("role", "").write();
                    this.props.history.push("/panel/login");
                    window.location.reload();
                  }}
                >
                  
                </button>
              </form>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    );
  }
}

export default Menu;
