import React, { Component } from "react";
import axios from "axios";
import Chart from "../../components/chart";
import config from "../../config";
import db from "../../helpers/localDB";
import { Container ,Row, Col , Card, CardTitle,CardText,CardGroup ,Button} from "reactstrap";
import { DatePicker} from "react-advance-jalaali-datepicker";
import moment from 'jalali-moment';
import ReactExport from 'react-export-excel'

const ExcelFile = ReactExport.ExcelFile
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn


class Panel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      incomeData: [
        { name: "", point: 120000 },
        { name: "", point: 150000 },
        { name: "", point: 90000 },
        { name: " ", point: 80000 },
        { name: "", point: 160000 },
        { name: " ", point: 212000 },
        { name: "", point: 250000 }
      ],
      tripData: [
        { name: "", point: 20 },
        { name: "", point: 33 },
        { name: "", point: 12 },
        { name: " ", point: 35 },
        { name: "", point: 85 },
        { name: " ", point: 43 },
        { name: "", point: 9 }
      ],
      chartData:[],
      chartDataByTime:[],
      driverCount:'',
      passengersCount:'',
      tripsCount:'',
      timeSelect:'',
      startDate:'',
      endDate:'',
      newArr:[]
      
    };
    this.loadCardsData();
    this.loadChartData();
   
  }
  async loadCardsData() {
    try {

        let result = await axios({
            method: 'get',
            url: config.app.BASE_URL + 'panel/stat/brief' ,
            headers: { 'Authorization': `Bearer ${db.get("token").value()}` },
        });
        if (result.data.status === 200) {
          this.setState({
            driverCount:result.data.stat.driversCount,
            passengersCount:result.data.stat.passengersCount,
            tripsCount:result.data.stat.tripsCount,

          });
        }


    } catch (e) {

        console.log(e.message);

    }

  }

  async loadChartData() {
    
    try {

        let result = await axios({
            method: 'get',
            url: config.app.BASE_URL + '/panel/stat/trips' ,
            headers: { 'Authorization': `Bearer ${db.get("token").value()}` },
          //   params:{'byDay[0]': 0,'byDay[1]':7,'byWeek[0]':0,'byWeek[1]':3,
          //  'byMonth[0]':0,'byMonth[1]':2
          //   }
          params:{'startDate':this.state.startDate,'endDate':this.state.endDate}
        });
        if (result.data.status === 200) {
          let tripData = result.data.trips.byDay.map((trip)=>{
              return {
                name:moment(trip.name).locale('fa').format('YYYY/MM/DD'),
                tripsCounts:trip.tripsCounts,
                tripsIncome:trip.tripsIncome
              }
          });
          // for(var i=0;i<result.data.trips.byDay.length;i++){

              // this.state.newArr.push({
              //  "name": result.data.trips.byDay[i]._id.year+'/'+result.data.trips.byDay[i]._id.month,
              //  "tripsIncome":result.data.trips.byDay[i].tripsIncome
              // })

          // }
        
          this.setState({
            chartData:result.data.trips,
            chartDataByTime:tripData,

          });
        }


    } catch (e) {

        console.log(e.message);

    }

  }

 
  handleChange= e => {
    this.setState({
      timeSelect:e.target.value,
      chartDataByTime:this.state.chartData[e.target.value]
    });
   
  
  }

  changestartDate = (e) => {

    this.setState({
     
      startDate:e

    });
  
  }
  changeEndDate = (e) => {

    this.setState({
     
      endDate:e
    

    });
   
  }

  getNewChart=()=>{
      this.loadChartData();
  }

  render() {
   
    return (
      <Container>
        <Row>
          <Col>
            <CardGroup>
              <Card body color="light">
                <CardTitle>  </CardTitle>
                <CardText>{this.state.tripsCount}</CardText>
              </Card>
              <Card body color="light">
                <CardTitle> </CardTitle>
                <CardText>{this.state.driverCount}</CardText>
              </Card>
              <Card body color="light">
                <CardTitle> </CardTitle>
                <CardText>{this.state.passengersCount}</CardText>
              </Card>
            </CardGroup>
            {/* <Card className='cardmargin'>
            <Row>
            <Col md='4'>
                <FormGroup>
                  <Label for="exampleSelect"> </Label>
                  <Input value={this.state.timeSelect}  onChange={this.handleChange}  type="select" name="select" id="selectTime">
                    <option value='byDay'></option>
                    <option value='byMonth'></option>
                    <option value='byWeek'></option>
              
                  </Input>
              </FormGroup>
            </Col>
           
            </Row>
            <Row>
         
          
            </Row>
            
            </Card> */}
            <Card className="cardmargin">
              <Row>
                <Col md="4">
                  <label> </label>
                  <DatePicker
                    placeholder="  "
                    format="jYYYY/jMM/jDD"
                    id="dateTimePicker"
                    onChange={this.changestartDate}
                  />
                </Col>
                <Col md="4">
                  <label> </label>
                  <DatePicker
                    placeholder="  "
                    format="jYYYY/jMM/jDD"
                    id="dateTimePicker2"
                    onChange={this.changeEndDate}
                  />
                </Col>
                <Col md="2">
                  <Button
                    color="info"
                    className="mrginTop"
                    onClick={this.getNewChart}
                  >
                    
                  </Button>
                </Col>
              </Row>
            </Card>

            <Chart
              chartData={this.state.chartDataByTime}
              datakey="tripsIncome"
              title="  "
            ></Chart>
            <Chart
              chartData={this.state.chartDataByTime}
              datakey="tripsCounts"
              title="  "
            ></Chart>
          </Col>
        </Row>
        <Row>
          <ExcelFile
            element={
              <button className="btn btn-success m-3 ">  </button>
            }
          >
            <ExcelSheet data={this.state.chartDataByTime} name="">
              <ExcelColumn label="" value="name" />
              <ExcelColumn label="  " value="tripsIncome" />
              <ExcelColumn label="  " value="tripsCounts" />
            </ExcelSheet>
          </ExcelFile>
        </Row>
      </Container>
    )
  }
}

export default Panel;
